:root {
  --background-light: rgb(246, 245, 245);
  --text-color1: #838383;
  --text-color2: seagreen;
  --background-dark: rgb(50, 50, 50);
}

html {
  font-family: 'Raleway', sans-serif;
}

body {
  margin: 0;
  padding: 0;
  height: 100%;
  overflow-x: hidden;
  font-size: 10px;

  background: var(--background-light);
  color: rgba(28, 28, 28, 0.95);
}

.content {
  min-height: calc(100vh - 2rem);
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none;   /* Chrome/Safari/Opera */
  -moz-user-select: none;      /* Firefox */
  -ms-user-select: none;       /* Internet Explorer/Edge */
  user-select: none;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: linear-gradient(#838383, var(--background-light));
}

h1 {
  margin: 0;
  padding: 2rem 0.5rem 2rem 2rem;
  font-size: 3rem;
}

header div {
  display: grid;
  justify-items: end;
  gap: 1rem;
  padding-right: 1rem;
  font-size: 1rem;
}

.date span{
  float: right;
}

.language{
  cursor: pointer;
  font-size: 1.2rem;
}

.language:hover, .language:focus {
  color: var(--text-color2);
  border-bottom: solid 1px var(--text-color2) ;
}

.loading {
  padding-left: 2rem;
  font-size: 2rem;
}

.show-currencies-mobile{
  padding: 0.5rem;
  font-size: 1rem;
  color: var(--background-light);
}

.currencies-block {
  background-color: var(--background-dark);
  color: var(--background-light);
}

.currencies-block-pc {
  display: flex;
  align-content: space-between;
  align-items: stretch;
  flex-wrap: wrap;
  overflow: hidden;
  height: 2rem;
}

.currencies-block-pc:hover, .currencies-block-pc:focus {
  margin-top: 0.5rem;
  align-self: baseline;
  overflow: visible;
  height: auto;
  z-index: 3;
}

.currencies-block div {
  width: 3rem;
  padding: 0.5rem;
  cursor: pointer;
  text-align: center;
  font-size: 1rem;
}

.currencies-block-mobile {
  display: none;
  width: auto;
  grid-template-columns: repeat(auto-fill, minmax(4rem, 1fr));
  height: auto;
  z-index: 3;
}

.currencies-block div:hover, .currencies-block div:focus{
  background-color: var(--text-color2);
}

.tooltip {
  position: relative;
  display: inline-block;
}

.tooltiptext {
  font-size: 0.8rem;
}

.tooltip .tooltiptext {
  position: absolute;
  padding: 0.2rem;
  max-height: 3rem;
  width: 8rem;
  bottom: 100%;
  left: -110%;
  background-color: var(--text-color2);
  color: var(--background-light);
  text-align: center;

  visibility: hidden;
  opacity: 0;
  transition: opacity 0.5s;
}

.tooltip:hover .tooltiptext, .tooltip:focus .tooltiptext {
  visibility: visible;
  opacity: 1;
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--background-dark);
  height: 3rem;
  gap: 1rem;
  padding-right: 0.5rem;
}

.form{
  flex-basis: content;
}
.search-input{
  margin-left: 2rem;
  padding: 0.2rem 0;
  background-color: var(--background-dark);
  font-size: 1rem;
  color: var(--background-light);
  width: 16rem;
  outline:none;
  border-radius: 0.1rem;
  border: var(--background-dark) solid;
  font-weight: normal;
  font-family: 'Raleway', sans-serif;
}

.search-input:hover, .search-input:focus {
  box-shadow: 0 0 2px 0 var(--text-color1);
  -webkit-box-shadow: 0 0 2px 0 var(--text-color1);
  -moz-box-shadow: 0 0 2px 0 var(--text-color1);
}

.autocomplete {
  position: absolute;
  padding: 0;
  margin: 0.8rem 0;
  width: 19rem;
  max-height: 20rem;
  overflow: auto;
  list-style: none;
  font-size: 1rem;
  color: var(--background-light);
  background-color: rgba(25, 25, 25, 0.9);
  z-index: 2;
}

.autocomplete::-webkit-scrollbar {
  width: 0.5rem;
  background-color: rgba(0, 0, 0, 0);
}

.autocomplete::-webkit-scrollbar-thumb {
  background-color: var(--text-color2);
}

.autocomplete li {
  padding: 0.5rem 0 0.5rem 2rem;
}

.autocomplete li:hover, .autocomplete li:focus{
  background-color: var(--text-color2);
  cursor: pointer;
  transition: cubic-bezier(1, 0, 0, 1) .3s all;
}


.inputs {
  display: flex;
  align-items: baseline;
  flex-wrap: wrap;
  gap: 1rem;
  padding: 2rem;
}

.input-item form label {
  font-size: 0.8rem;
}

.input-item label{
  display: inline-block;
  padding: 0.2rem;
  font-size: 1rem;
  color: var(--text-color1);
}


.delete {
  margin-left: 4rem;
  padding: 0.1rem 1rem;
  position: absolute;
  font-size: 0.8rem;
  cursor: pointer;

  opacity: 0;
  transition: opacity 0.5s;
}

.input-item:hover .delete, .delete:focus {
  visibility: visible;
  opacity: 1;
}

.delete:hover, .delete:focus {
  color: red;
}

.input-value {
  display: block;
  margin-bottom: 1rem;
  max-width: 10rem;
  height: 2rem;

  outline:none;
  border: none;
  background: transparent;

  font-family: 'Raleway', sans-serif;
  font-size: 1.5rem;
}

footer {
  padding: 0 2rem;
}

footer a:hover, footer a:focus{
  color: var(--text-color2);
}

footer a:visited{
  color: var(--text-color1);
}

@media screen and (min-width: 1241px) {
  .show-currencies-mobile {
    display: none;
  }
}

@media screen and (max-width: 1240px) {
  .show-currencies-mobile {
    display: block;
  }

  .currencies-block-pc {
    display: none;
  }

  .tooltip .tooltiptext {
    display: none;
  }
}

@media screen and (max-width: 900px) {
  h1{
    font-size: 2.5rem;
  }

  .inputs {
    padding: 1rem 1rem 1rem 2rem;
    gap: 0.5rem;
  }
}

@media screen and (max-width: 470px) {
  html {
    font-size: 14px;
  }

  header div {
    display: grid;
    justify-items: end;
    gap: 0.2rem;
    padding-right:0.5rem;
    font-size: 0.7rem;
  }

  h1{
    padding: 1rem;
    font-size: 2rem;
  }

  .language{
    font-size: 1rem;
  }

  .loading {
    padding-left: 1rem;
    font-size: 1.5rem;
  }

  .search-input {
    width: 12rem;
    margin-left: 1rem;
  }

  .autocomplete{
    width: 100%
  }

  .autocomplete li {
    padding-left: 1rem;
  }

  .inputs {
    padding: 1rem;
    gap: 0.5rem;
  }
}





